import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ConstantsService, PRIVILEGES} from '../services/constants.service';
import {Router} from '@angular/router';
import {ControllerService} from '../services/controller.service';

@Component({
  selector: 'app-admin-products',
  templateUrl: './admin-products.page.html',
  styleUrls: ['./admin-products.page.scss'],
})
export class AdminProductsPage implements OnInit {

  @ViewChild('wears') wears: ElementRef;
  @ViewChild('groceries') groceries: ElementRef;
  items: any[] = Object.values(this.api.getProducts());
  wearItems = [];
  groceriesItems = [];
  constructor(private router: Router, private api: ControllerService, private currency: ConstantsService) { }

  ngOnInit() {
    const user = this.api.getCurrentUser();
    if (user !== PRIVILEGES.ADMIN) {
      this.router.navigateByUrl('/home');
    }

    this.categoriesProducts();
  }

  categoriesProducts() {
    this.wearItems = [];
    this.groceriesItems = [];

    this.items.forEach((item: any) => {
      if (item.category === 'wears') {
        this.wearItems.push(item);
      } else if (item.category === 'groceries') {
        this.groceriesItems.push(item);
      }
    });
  }

  switch($event: CustomEvent) {
    if ($event.detail.value === 'wears') {
      this.wears.nativeElement.style.display = 'block';
      this.groceries.nativeElement.style.display = 'none';
    } else {
      this.wears.nativeElement.style.display = 'none';
      this.groceries.nativeElement.style.display = 'block';
    }
  }

  deleteProduct(id: string) {
    this.api.deleteProduct(id);
    this.items = Object.values(this.api.getProducts());
    this.categoriesProducts();
  }

  logout() {
    this.api.logout(PRIVILEGES.ADMIN);
    location.assign('/home');
  }
}
